
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.wrapper {
  min-height: 100vh;
  background-color: #f5f5f5;
}
.page-header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background-color: #ffffff;
  -webkit-transition: all .3s;
  transition: all .3s;
  z-index: 100;
  margin-bottom: 25px;
}
.collapse {
  background-color: #fff;
  -webkit-box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
          box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
  opacity: 0.98;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 48px;
  border-radius: 4px;
  padding: 24px 0 0;
  margin-bottom: 24px;
  background-color: #ffffff;
  min-height: 100vh;
}



